@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
* {
	margin: 0;
}
body {
	background: #000;
}

div.under-construction {
	height: 100vh;
	width: 100vw;
	color: #fff;
	font-family: 'Noto Sans', sans-serif;
}
div.under-construction-flex-container {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	height: 100%;
}
div.under-construction-flex-container>h1 {
	font-size: 10vmin;
}

div.home {
	height: 100vh;
	width: 100vw;
	color: #fff;
	font-family: 'Noto Sans', sans-serif;
}
div.home-flex-container {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	height: 100%;
}
div.home-flex-container>h1 {
	font-size: 10vmin;
}
div.home-flex-container>a {
	text-decoration: none;
	color: #76906d;
}

